import React from 'react';
import { Text } from '@sitecore-jss/sitecore-jss-react';
import { withTranslation} from 'react-i18next';

const Quote = (props) => {
  const { backgroundColor, fontColor, fontSize } = props.params;
  const {quote, author} = props.fields;

  return (
    <div style={{backgroundColor: backgroundColor, color: fontColor}}>
      <div>{props.t('TodaysQuoteLabel')}:</div>
      <div style={{fontSize: parseInt(fontSize)}} >
        "<Text field={quote}/>"
      </div>
      <div style={{textAlign: 'right'}}>
        <Text field={author} />
      </div>
    </div>
  );
};
  
export default withTranslation()(Quote);